@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@100;300&display=swap);
body{
  height: 100vh;
  overflow: hidden;
  background:linear-gradient(rgb(255, 79, 79),rgb(255, 100, 126));
}
.container{
    width:28%;
    margin:0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding:80px 0px;
    font-family: 'Comfortaa', cursive;
}
h1{
    font-size:26px;
    color:white;
}
.item{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    height: 30px;
    -webkit-align-items: center;
            align-items: center;
    margin: 10px 0;
}
.button-bar{
    width: 12%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
i{
    cursor: pointer;
}
.done{
    text-decoration:line-through;
}
.item-done-button{
    color:rgba(94, 255, 0, 0.6);
}
.item-delete-button{
    color:rgba(255, 0, 0, 0.6)
}
.item-done-button:hover{
    transition:ease-in 400ms ;
    color:rgb(94, 255, 0);
}
.item-delete-button:hover{
    transition:ease-in 400ms;
    color:rgb(255, 0, 0);
}
.item-list{
    margin: 25px 0;
    width: 100%;
    box-sizing: border-box;
    background-color: white;
    padding:20px;
}
.add-panel{
    width: 100%;
    height: 60px;
    font-size:20px;
}
.add-info{
    box-sizing: border-box;
    width: 80%;
    height: inherit;
    outline: none; 
    border: none; 
    padding: 5px 15px;
}
.add-button{
    box-sizing: border-box;
    width: 20%;
    height: inherit;
    border: none;
    background-color: white;
    text-align: center;
    outline: none;
    cursor: pointer;
}
